import uuidv4 from 'uuid/v4'
import { ColorOrganizerHeader } from './header/color-organizer-header.js'
import { ColorList } from './color/colorList.js'

import './colorOrganizer.scss'

export default class ColorOrganizer extends React.Component {

  constructor (props) {
    super(props)
    this.state = {
      colors: [
        { 
          color: { backgroundColor: '#004080' },
          title: "favorite blue",
          starSelected: 3,
          id: "2345"
        },
        { 
          color: { backgroundColor: '#eee' },
          title: "geeky grey",
          starSelected: 5,
          id: "2347"
        },
        { 
          color: { backgroundColor: '#0096ce' },
          title: "great blue",
          starSelected: 5,
          id: "2348"
        }
      ],
      starLimit: 5,
      formIsOpen: false
    }
    this.onAddColor = this.onAddColor.bind(this)
    this.onRate = this.onRate.bind(this)
    this.onRemove = this.onRemove.bind(this)
  }

  onAddColor(color, title) {
    const newColor = {
      color: color,
      title: title,
      id: uuidv4()
    }
    const newColors = [
      ...this.state.color,
      newColor
    ]
    this.setState({ colors: newColors })
  }

  onClickForm () {
    const current = this.state.formIsOpen
    this.setState({ formIsOpen: !current })
  }

  onRate (num, id) {
    const { colors } = this.state
    const newColors = colors.map((color) => {
      if (color.id === id) {
        color.starSelected = num
      }
      return color
    })
    this.setState({
      colors: newColors
    })
  }

  onRemove (id) {
    const msg = `you tried to remove but you cant!! id: ${id}`
    window.alert(msg)
  }

  render () {
    const { onAddColor, onRate, onRemove } = this
    const { colors, starLimit, formIsOpen } = this.state

    return (
      <div className="color-organizer-container">
        <ColorOrganizerHeader formIsOpen={formIsOpen}
          onAddColor={onAddColor} />
        <ColorList colors={colors}
          starLimit={starLimit}
          onRate={onRate}
          onRemove={onRemove} />
      </div>
    )
  }
}



